.artistasSeleccion {
    color: rgb(0, 153, 255);
    font-family: "Josefin Sans", sans-serif;
    line-height: 2;
    font-size: large;
    width: fit-content;
    animation: fadeInLeft 2s 0.2s ease-in; }

#ilustradores {
    display: inline-block;
    padding: 0.5rem 1.7rem 1rem 2rem;
    background-color: rgba(197, 197, 197, 0.2);
    border-color: azure;
    border-style: solid;
    border-width: 1px; }

.slidegallery {
    color: rgba(197,197,197,0.2);
    text-align: center;
    background-color: azure;
    border-color: azure;
    border-style: solid;
    border-width: 1px;
    position: relative;
    overflow: hidden;
    height: 100%;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    img {
        &:hover {
            cursor: zoom-in; } }
    a {
        border-radius: 50%;
        margin: 0 3px;
        color: rgba(0,0,0,0.8);
        text-decoration: none; }
    &.slider {
        height: 100%;
        white-space: nowrap;
        font-size: 0;
        &* {
            display: inline-block;
            white-space: normal;
            vertical-align: top;
            height: 100%;
            width: 100%; } }
    .prevNext {
        position: absolute;
        z-index: 1;
        top: 50%;
        width: 100%;
        height: 0;
        a {
            background: rgba(197, 197, 197, 0.2);
            position: absolute;
            width: 50px;
            height: 50px;
            top: 50%;
            opacity: 0.7;
            left: 0;
            &:hover {
                opacity: 1; } } } }

#descripcion {
    display: flex;
    flex-grow: 2;
    flex-flow: wrap;
    width: 40vw; }

figure {
    img {
        border-style: solid;
        background-color: white;
        border-color: azure;
        width: 300px; } }
.introduccion {
    color: rgb(58,50,50);
    line-height: 1.8;
    font-size: 120%;
    font-family: "Josefin Slab", serif;
    font-style: oblique;
    padding-right: 2.2rem;
    padding-left: 2.2rem;
    font-weight: 600; }

.boton-inicio {
    position: sticky;
    top: 490px;
    display: inline-block;
    width: 50px;
    left: 90vw;
    z-index: 3; }

.video {
    width: 400px;
    height: 400px;
    border: rgb(58, 50, 50) ridge 3px;
    width: 300px;
    height: 300px;
    margin: 2rem;
    display: inline-block;
    animation: fadeInLeft 2s 0.2s ease-in; }

.biografia {
    color: rgb(58, 50, 50);
    font-family: "Josefin Slab", serif;
    font-weight: bolder;
    line-height: 1.5;
    font-size: large;
    display: inline-block;
    position: absolute;
    margin: 1rem;
    padding: 0.5rem;
    width: 30rem; }

#redesIlustrador {
    position: absolute;
    display: inline-block;
    padding-left: 550px;
    padding-top: 20px;
    animation: fadeInRight 2s 0.2s ease-in; }

.redes {
    width: fit-content;
    li {
        color: purple;
        text-decoration: none;
        font-size: large;
        line-height: 1.6;
        font-family: "Josefin Slab", serif;
        a {
            color: purple;
            text-decoration: none;
            font-size: large;
            line-height: 1.6;
            font-family: "Josefin Slab",serif; } } }

#sugerencias {
    width: 96%;
    height: 200px;
    padding: 0.2rem;
    p {
        &.nombre-producto {
            font-family: "Josefin Sans", sans-serif; } }
    img {
        width: 140px; }
    a {
        &:hover {
            color: rgb(105,210,231); } }
    ol {
        overflow: hidden;
        overflow-x: scroll;
        display: flex;
        flex-flow: nowrap;
        flex-direction: row;
        margin: 0em;
        padding: 0.5rem;
        li {
            div {
                width: 145px;
                display: inline-flex;
                height: 220px;
                a {
                    div {
                        width: 145px;
                        background-color: rgba(243, 252, 253, 0.658); } } } } }
    li {
        width: fit-content;
        list-style: none;
        margin: 0.2rem;
        padding: 0.3rem;
        background-color: rgba(197, 197, 197, 0.2); } }

#producto-compra {
    display: flex;
    flex-flow: column;
    flex-wrap: wrap; }

#producto-descripcion {
    display: flex;
    flex-flow: column;
    flex-wrap: wrap; }

#diseno-descripcion {
    border: rgb(224, 228, 204) ridge 2px;
    font-size: medium;
    font-family: "Josefin Slab", serif;
    line-height: 1.3;
    width: fit-content;
    display: inline-block;
    margin-top: 50px;
    border-spacing: 0.1rem;
    border-collapse: separate;
    td {
        padding: 0.4rem; }
    th {
        padding: 0.4rem; }
    tr {
        padding: 0.4rem;
        &:nth-child(even) {
            background-color: rgba(192, 216, 216, 0.664); }
        &:nth-child(odd) {
            background-color: rgb(220, 247, 243); }
        &:hover {
            background-color: rgba(32,153,209,0.479); } }
    caption {
        background-color: azure; } }

#compra {
    margin: 0;
    color: rgb(58,50,50);
    p {
        color: rgb(58,50,50);
        font-weight: 600; }
    input {
        background-color: azure;
        padding: 0.4rem;
        margin-left: 1rem;
        width: 6.5rem;
        &:hover {
            background-color: rgb(105,210,231); } } }
#precio {
    width: 4rem; }
#cantidad {
    width: 4rem; }

@-webkit-keyframes fadeInLeft {
    from {
        opacity: 0;
        -webkit-transform: translate3d(-100%, 0, 0);
        transform: translate3d(-100%, 0, 0); }
    to {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0); } }

@keyframes fadeInLeft {
    from {
        opacity: 0;
        -webkit-transform: translate3d(-100%, 0, 0);
        transform: translate3d(-100%, 0, 0); }
    to {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0); } }

.fadeInLeft {
    -webkit-animation-name: fadeInLeft;
    animation-name: fadeInLeft; }

@-webkit-keyframes fadeInRight {
    from {
        opacity: 0;
        -webkit-transform: translate3d(100%, 0, 0);
        transform: translate3d(100%, 0, 0); }
    to {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0); } }
@keyframes fadeInRight {
    from {
        opacity: 0;
        -webkit-transform: translate3d(100%, 0, 0);
        transform: translate3d(100%, 0, 0); }
    to {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0); } }
.fadeInRight {
    -webkit-animation-name: fadeInRight;
    animation-name: fadeInRight; }
@-webkit-keyframes slideInDown {
    from {
        -webkit-transform: translate3d(0, -100%, 0);
        transform: translate3d(0, -100%, 0);
        visibility: visible; }
    to {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0); } }
@keyframes slideInDown {
    from {
        -webkit-transform: translate3d(0, -100%, 0);
        transform: translate3d(0, -100%, 0);
        visibility: visible; }
    to {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0); } }
.slideInDown {
    -webkit-animation-name: slideInDown;
    animation-name: slideInDown; }
