footer {
    border-top: solid rgb(0,153,255);
    background-color: black;
    ul {
        margin: 0;
        padding-top: 0;
        padding-bottom: 0;
        font-size: 0;
        padding: 2rem;
        li {
            color: rgb(0,153,255);
            font-family: "Josefin Sans", sans-serif;
            display: inline-block;
            width: 20%;
            text-align: center;
            font-size: 20px;
            border-right: solid 1px;
            box-sizing: border-box;
            position: relative;
            padding: 0.7rem;
            &:last-child {
                border-right: none; }
            &:hover {
                background-color: azure; } } } }
/*Segunda parte del footer*/
.contacto {
    text-decoration-line: none;
    color: rgb(58,50,50);
    line-height: 1.6;
    list-style: none;
    font-family: "Josefin Slab",serif;
    font-weight: 600;
    ul {
        font-size: 0;
        padding: 2rem;
        li {
            color: rgb(105,210,231);
            font-family: "Josefin Sans", sans-serif;
            display: inline-block;
            width: 16%;
            text-align: center;
            font-size: 20px;
            border-right: solid 1px;
            box-sizing: border-box;
            position: relative;
            padding: 0.7rem;
            &:last-child {
                border-right: none; }
            &:hover {
                background-color: azure; } } } }
