#div-index-grid {
    width: 99vw;
    border-style: solid;
    border-width: 1px;
    border-color: rgba(197, 197, 197, 0.2);
    vertical-align: baseline;
    display: grid;
    grid-template-areas: "titulo titulo titulo" "ilustradores gallery descripcion" "autor autor autor" "sugerencias sugerencias sugerencias" "compra p-descripcion p-descripcion" "carrito carrito carrito" "suscribe suscribe suscribe";
    grid-template-rows: 60px 400px 480px 400px 450px 240px 630px;
    grid-template-columns: 28vh 30vw 36vw;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
    align-content: center;
    &.seccion {
        grid-area: titulo; }
    #ilustradores {
        grid-area: ilustradores; }
    &.slideGallery {
        grid-area: gallery; }
    #descripcion {
        grid-area: descripcion; }
    &.autor {
        grid-area: autor; }
    #sugerencias {
        grid-area: sugerencias; }
    #producto-compra {
        grid-area: compra; }
    &.producto-descripcion {
        grid-area: p-descripcion; }
    #carrito {
        grid-area: carrito; }
    &.suscribe.wow.fadeInUp {
        grid-area: suscribe; } }

#main-noticias-grid {
    display: grid;
    grid-template-areas: "seccion" "noticias" "suscribe";
    grid-template-rows: 25px 700px 700px;
    grid-template-columns: 96vw;
    grid-column-gap: 5px;
    grid-row-gap: 15px;
    &.seccion {
        grid-area: seccion; }
    &.noticias {
        grid-area: noticias; }
    &.suscribe {
        grid-area: suscribe; } }

#main-vertodo-grid {
    width: 97vw;
    display: grid;
    grid-template-areas: "seccion seccion" "detalle disponible" "compra p-descripcion" "carrito carrito";
    grid-template-rows: 60px 660px 450px 240px;
    grid-template-columns: 40vw 56vw;
    grid-column-gap: 15px;
    grid-row-gap: 10px;
    &.seccion {
        grid-area: seccion; }
    &.detalle-producto {
        grid-area: detalle; }
    &.disponibles {
        grid-area: disponible; }
    #producto-compra {
        grid-area: compra; }
    &.producto-descripcion {
        grid-area: p-descripcion; }
    #carrito {
        grid-area: carrito; } }

#usuario-grid {
    display: grid;
    grid-template-areas: "titulo titulo" "nombre-usuario tab-usuario" "card-usuario tab-usuario" "carrito proceso-compra";
    grid-template-rows: 60px 60px 300px 300px;
    grid-template-columns: 36vw 58vw;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
    align-content: center;
    &.seccion {
        grid-area: titulo; }
    &.name-usuario {
        grid-area: nombre-usuario; }
    &.usuario {
        grid-area: card-usuario; }
    &.tab-usuario {
        grid-area: tab-usuario; }
    #carrito {
        grid-area: carrito; }
    &.proceso-de-compra {
        grid-area: proceso-compra; } }
